.wrapper {
  grid-template: "header header"
                 "sidebar main" 1fr
                 / auto 5fr;
  height: 100vh;
  display: grid;
  overflow: hidden;
}

nav {
  grid-area: header;
}

aside {
  grid-area: sidebar;
  min-width: 280px;
  height: 100%;
  overflow-y: scroll;
}

main {
  grid-area: main;
  overflow-y: scroll;
}

.example {
  border-radius: var(--bs-border-radius-xl);
  border: 1px dashed var(--bs-border-color);
  padding: 1.5rem;
}

