.wrapper {
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-columns: auto 5fr;
  grid-template-areas:
    "header header"
    "sidebar main";
  height: 100vh;
  overflow: hidden;
}

nav {
  grid-area: header;
}

aside {
  grid-area: sidebar;
  min-width: 280px;
  height: 100%;
  overflow-y: scroll;
}

main {
  grid-area: main;
  overflow-y: scroll;
}

.example {
  padding: 1rem * 1.5;
  border-radius: var(--bs-border-radius-xl);
  border: 1px dashed var(--bs-border-color);
}
